import * as React from "react";
import {Link} from "react-router";

export interface LinkProps {
  handleChange: Function ,
  value: {
    contentCode: string,
    id: string,
    title: string
  }
}

export default class LinkAttr extends React.Component<LinkProps, undefined> {

  constructor(props: LinkProps) {
    super(props);
  }


  render() {
    var value = this.props.value;
    if (value != null) {
      return <Link to={'/obj/'+value.contentCode+'/'+ value.id}>{value.title}</Link>
    } else {
      return <div></div>
    }
  }
}









